#header-main_gallery {
  padding: 0 20px;
}

.gallery_nav {
  width: 100%;
  height: 60px;
  max-width: 1200px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.gallery_nav ul {
  gap: 10px;
  list-style: none;
  display: flex;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

#lightbox {
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .8);
  display: none;
  position: fixed;
  top: 0;
}

#lightbox.active {
  justify-content: center;
  align-items: center;
  display: flex;
}

